import * as React from "react";
//import  {FormControl} from "react-bootstrap";
var Codemirror = require("react-codemirror/lib/Codemirror");

require('codemirror/lib/codemirror.css');
require('codemirror/theme/eclipse.css');
require('codemirror/mode/javascript/javascript.js');


export interface StringProps {
  handleChange: any ,
  value: string
}


export default class StringCode extends React.Component<StringProps, undefined> {

  constructor(props: StringProps) {
    super(props);
  }

  funk(doc: any, change: any) {
    return this.props.handleChange(doc, (doc: any) => doc);
  }


  render() {
    const options = {
      lineNumbers: true,
      mode: 'javascript',
      theme: 'eclipse'
    };
    return <Codemirror value={this.props.value} onChange={(doc: any, change: any)=>{this.funk(doc,change)}}
                       options={options}/>
    //   return <FormControl componentClass="textarea" onChange={this.props.handleChange} value={this.props.value}/>
  }
}






